import { RendererCanvas } from '../../../../lib';

export class Solution extends RendererCanvas {

    constructor(div: HTMLDivElement) {
        super(div);

        this.canvas.style.background = '#fff';
        this.ctx.translate(this.ctx.canvas.width * 0.5, this.ctx.canvas.height * 0.5);
        this.ctx.scale(2, 2);
        this.init();
    }
    public init() {
        const ctx = this.ctx;

        // ctx.beginPath();
        // ctx.arc(0, 0, 2, 0, 3.14 * 2)
        // ctx.closePath();

        // ctx.fillStyle = '#ff0000'
        // ctx.fill();

        // TODO
        // You code goes here for one time operation
        // Reference: https://codepen.io/NJStudio/pen/jObdYOr?editors=1010


        


        this.renderForce(ctx)

    }
    renderForce(ctx: CanvasRenderingContext2D) {
        const N = 6;

        const xC = 0
        const yC = 0
        const radius = 50

        const angle = Math.PI * 2 / N
        const angleStart = Math.PI

        const color = ['#f00', '#0f0', '#00f']

        for(let i = 0 ; i < N; ++i) {

            const radian = angleStart + (angle * i);
            console.log(radian)
            const x = xC + Math.sin(radian) * (radius);
            const y = yC + Math.cos(radian) * (radius);

            ctx.fillStyle = color[i]
            ctx.beginPath();
            ctx.arc(x, y, 1, 0, Math.PI * 2)
            ctx.closePath();
            ctx.fill()
        }
    }
}


